import React from 'react';
import './Details.css';
import { Container } from 'react-bootstrap';
import About from '../about/About';
import Photos from '../photos/Photos';
import data from '../../data/data.json';
import { Route, Switch } from 'react-router-dom';
import Resume from '../resume/Resume'
class Details extends React.Component {
	render() {
		return (
			<div className="box">
				<div className="profile">
					<img className="image" alt="display" src={require(`../../images/${data.Image}`)} />
					<p className="text">{data.Name}</p>
					
				</div>
				<Container className="about">
					<Switch>
						<Route exact path="/" component={About} />
						<Route exact path="/me/" component={About} />
						<Route exact path="/me/Photos" component={Photos} />
						<Route exact path="/me/Resume" component={Resume} />
					</Switch>
				</Container>
			</div>
		);
	}
}

export default Details;
