import React from 'react';
import { Carousel } from 'react-bootstrap';
import data from "../../data/data.json";
import './Corousel.css';
const CorouselBack = () => {
    return (
        <Carousel className = "cor">
            {
                data.Corousel.map(
                    image => <Carousel.Item>
                        <img
                        className="w-100"
                        src= {require(`../../images/${image}`)}
                        alt={image}
                        />
                    </Carousel.Item>
                )
            }
            
            </Carousel>
    )
}
export default CorouselBack;