import React from 'react';
import { Navbar, Nav } from 'react-bootstrap';
import data from "../../data/data.json";
import './Header.css';
import { Link } from 'react-router-dom';
import HomeIcon from '@material-ui/icons/Home';
import Avatar from '@material-ui/core/Avatar';
import GitHubIcon from '@material-ui/icons/GitHub';
import LinkedInIcon from '@material-ui/icons/LinkedIn';
import EmailIcon from '@material-ui/icons/Email';
class HeaderWhite extends React.Component {
    render() {
        return(
            <div className="white">
                <Navbar bg='light' expand="md" className = "nav-border" >
                    <Navbar.Toggle aria-controls="basic-navbar-nav" />
                    <Navbar.Collapse id="basic-navbar-nav" style={{"z-index": 1}}>
                        <Nav className="mr-auto" varient = 'success' defaultActiveKey="/home">
                            <Link className="nav-link scroll" to="/me"><HomeIcon style={{ color: 'black', position: 'relative', top : '-3px'}}/>  {'\u00A0'} Home</Link>
                            <Link className="nav-link" to="/me/Resume">Resume</Link>
                            <Link className="nav-link" to="/me/Photos">Photos</Link>
                            <Link className="nav-link" to="/me/blog">Blog</Link>
                        </Nav>
                        <Avatar alt="name" src={require(`../../images/${data.Image}`)} />
                        <Navbar.Brand className='heading' href="#">{data.Name}</Navbar.Brand>

                        <Nav className="ml-auto" varient = 'success'>
                            
                            <Nav.Link href={`mailto:${data.Email}`} target="_blank">
                                <EmailIcon color="primary" />
                            </Nav.Link>
                            <Nav.Link href={`https://linkedin.com/in/${data["Social Media"].LinkedIn}`} target="_blank">
                                <LinkedInIcon color="primary" />
                            </Nav.Link>
                            <Nav.Link href={`https://github.com/${data["Social Media"].Github}`} target="_blank">
                                <GitHubIcon color="primary"/>
                            </Nav.Link>
                        </Nav>
                    </Navbar.Collapse>
                </Navbar>
            </div>
        )
    }
}
export default HeaderWhite;